import React, {Fragment} from "react";

const BASE_URL_IMDB = "https://image.tmdb.org/t/p/w500";

const VideoDetails = ({movie}) => {
    const date = new Date(movie.release_date).toLocaleDateString("fr-FR", {
        year: "numeric",
        month: "long",
        day: "2-digit"
    })
    return (
        <Fragment>
            <div className="videoDetails_cover">
                <img 
                    src = {`${BASE_URL_IMDB}${movie.poster_path}`}
                    alt = {`Affiche du film : ${movie.original_title}`}
                    width = "250"
                />
            </div>
            <div className="videoDetails_cover">
                <h2 className="text-center">{ movie.title } <small className="text-secondary">- { movie.id }</small></h2>
                <hr />
                <p className="lead">{ movie.overview }</p>
                <ul className="list-group">
                    <li className="list-group-item">Titre original : { movie.original_title }</li>
                    <li className="list-group-item">Langue original : { movie.original_language }</li>
                    <li className="list-group-item">Date de sortie : { date }</li>
                </ul>
            </div>
        </Fragment>
    )
}

export default VideoDetails;