import React, { Component } from 'react';
import './App.scss';

// Container
import VideoList    from './VideoList';

// Components
import SearchBar    from "../components/SearchBar";
import Video        from '../components/Video';
import VideoDetails from "../components/VideoDetails";

// Librairies
import axios        from "axios"

// Constantes The Movie Database
const API_END_POINT       = "https://api.themoviedb.org/3/";
const API_KEY             = "api_key=e3848ee01e33080bf2aeb19aef66d5db";
const URL_POPULAR_MOVIES  = "discover/movie?language=fr&sort_by=popularity.desc&include_adult=false&append_to_response=images";
const URL_SEARCH          = "search/movie?language=fr&include_adult=false";



class App extends Component {

  constructor(props) {
    super(props);
    this.state = { 
      movies: {}, 
      currentMovie: {}
    }
  }

  // Avant le render
  UNSAFE_componentWillMount() {
    this.initMovies();
  }



  initMovies() {
    axios
    .get(`${API_END_POINT}${URL_POPULAR_MOVIES}&${API_KEY}`)
    .then(async response => {
      
      const result = await response.data.results;
      /*
        console.groupCollapsed("r√©sultat de ma constante : result");
          console.table(result);
        console.groupEnd();
      */

      this.setState({ 

        movies: result.slice(1,10),
        currentMovie: result[0]
      }, () => {
        // Appel d'une fonction de callback pour r√©cup√©rer la vid√©o
        this.applyVideoToCurrentMovie()
      });

      /* 
        console.groupCollapsed("voir le r√©sultat du .slice(1,6)")
          console.table(result.slice(1,6))
        console.groupEnd();

        console.groupCollapsed("voir l'√©l√©ment 0 de ma liste de film r√©cup√©r√©")
          console.table(result[0])
        console.groupEnd()
      */
    })
    .catch(error => {
      console.error(error);
    })
  }

  applyVideoToCurrentMovie() {
    axios
      .get(`${API_END_POINT}movie/${ this.state.currentMovie.id }?${API_KEY}&append_to_response=videos&include_adult=false`)
      .then( async response => {

        // r√©cup√©ration de la r√©f√©rence de la video (key)
        const youtubeKey = await response.data.videos.results[0].key;

        // Conception d'un nouveau tableau
        let newCurrentMovieState = this.state.currentMovie;

        // Ajout de la r√©f√©rence de la vid√©o dans le nouveau tableau 
        newCurrentMovieState.videoId = youtubeKey;

        // Remplacement du state principal
        this.setState({
          currentMovie: newCurrentMovieState
        })
      })
      .catch(error => {
        console.error(error)
      })
  }
  
  onClickListItem(movie) {
    this.setState( {currentMovie: movie}, function() {
      this.applyVideoToCurrentMovie();
      this.setRecommendation();
    } )
  }

  onClickSearch = (searchText) => {

    if ( searchText ) {
      axios
        .get(`${API_END_POINT}${ URL_SEARCH }&${API_KEY}&query=${searchText}`)
        .then( response => {
          console.log(response)
          
          if ( response.data && response.data.results[0] ) {

            if ( response.data.results[0].id !== this.state.currentMovie.id ) {
              
              this.setState({ currentMovie: response.data.results[0] }, () => {
                this.applyVideoToCurrentMovie();
                this.setRecommendation();
              })
            }
          }

          if ( response.data.results[0].genre_ids.length === 0 ) {

            const paragrapheError = document.querySelector('#error');
            paragrapheError.style.color = "salmon";

            
            setTimeout(() => {
              paragrapheError.innerHTML = `Hum... Chargement trop long, <strong>redirection automatique</strong> vers un film similaire. <strong>Veuillez patienter</strong>...`;
            }, 3000, setTimeout(() => {              
              this.setState({currentMovie: response.data.results[1]}, () => {
                this.applyVideoToCurrentMovie();
                this.setRecommendation();
              });
              paragrapheError.innerHTML = "";
            }, 8000));
          }
        })
        .catch(error => {
          console.error(error);
        })
    }
    
  }

  /*
    ${API_END_POINT}movie/${this.state.currentMovie.id}/recommendations?${API_KEY}&language=fr
  */

  setRecommendation() {
    axios
      .get(`${API_END_POINT}movie/${this.state.currentMovie.id}/recommendations?${API_KEY}&language=fr`)
      .then(async response => {
        const resultRecommendation = await response.data.results;  
        this.setState({ movies: resultRecommendation.slice(0,9) });
      })
  }

  render() {

    return (      
      <>
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
          <a href="/" className="navbar-brand">Allo ReactMovie</a>
          <span className="navbar-text ml-auto">SPA cod√© avec passion</span>
        </nav>

        <div className="container mt-5">
          <div className="input-group">
            <SearchBar callback = { this.onClickSearch } />
          </div>
          <p id="error" className="mt-3"></p>
        </div>

        <section className="container-fluid">
          <article className="container_article">
            <div className="container_videoDetails">
              <VideoDetails movie = { this.state.currentMovie } />
            </div>
            <div className="embed-responsive embed-responsive-16by9">
              <Video videoId = { this.state.currentMovie.videoId } />
            </div>
          </article>

          <aside className="container_aside">
              { this.state.movies.length >= 5 ? (
                <VideoList 
                  movieList = { this.state.movies }
                  callback = { this.onClickListItem.bind(this) }
                />
              ) : (
                <img src="https://media.giphy.com/media/3oEjI6SIIHBdRxXI40/giphy.gif" alt="loading gif" width="80" height="80" />
              ) }
            </aside>
        </section>
      </>
    )
  }

}

export default App;
